export default {
	name: 'cu-cell',
	props: {
		height: {
			type: Number,
			default: 70
		},
		title: {
			type: String,
			default: ''
		},
		isIcon: {
			type: Boolean,
			default: false
		},
		icon: {
			type: Object,
			default: () => {
				return {
					type: '',
					color: '',
					size: 0
				}
			}
		},
		isLink: {
			type: Boolean,
			default: false
		},
		url: {
			type: String,
			default: ''
		},
		params: {
			type: String,
			default: ''
		},
		isLastCell: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
		}
	},
	methods: {
		navigateTo () {
			let url = this.url
			if (this.params) {
				url += '?'+this.params
			}
			uni.navigateTo({
				url: url
			})
		},
		handleCell () {
			if (this.isLink) {
				this.navigateTo()
			} else {
				this.$emit('clickCell')
			}
		}
	}
}
